"use client"

import { useEffect, useState, useCallback, useRef } from "react"
import { useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Card } from "@/components/ui/card"
import { Avatar, AvatarFallback } from "@/components/ui/avatar"
import { Tabs, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { ChevronLeft, Send, Users, User, MessageSquare, Circle, LogOut, Loader2 } from "lucide-react"
import { Client } from '@stomp/stompjs'
import SockJS from 'sockjs-client'

interface ChatMessage {
  id?: string;
  senderId: string;
  senderName: string;
  content: string;
  createdAt: string;
}

export default function RealTimeChatPage() {
  const router = useRouter()
  const [userId, setUserId] = useState("")
  const [userName, setUserName] = useState("")
  
  const [activeTab, setActiveTab] = useState("GROUP") 
  const [rooms, setRooms] = useState([])
  const [currentRoom, setCurrentRoom] = useState<any>(null)
  const [messages, setMessages] = useState<ChatMessage[]>([])
  const [inputValue, setInputValue] = useState("")
  const [isConnected, setIsConnected] = useState(false)
  const [isLoadingHistory, setIsLoadingHistory] = useState(false)
  
  const stompClient = useRef<Client | null>(null)
  const scrollRef = useRef<HTMLDivElement>(null)
  const subscriptions = useRef<Record<string, any>>({})

  const API_BASE = 'http://sparta-nextme.xyz:18080'

  // 1. 이전 대화 내역 불러오기
  const loadChatHistory = async (roomId: string) => {
    const token = localStorage.getItem("accessToken")
    setIsLoadingHistory(true)
    try {
      const res = await fetch(`${API_BASE}/v1/chats/room/${roomId}/messages`, {
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      const rawMessages = data.result || data.messages || (Array.isArray(data) ? data : [])
      setMessages(rawMessages.map((msg: any) => ({
        ...msg,
        id: msg.chatMessageId || msg.id || crypto.randomUUID()
      })))
    } catch (e) {
      setMessages([])
    } finally {
      setIsLoadingHistory(false)
    }
  }

  // 2. 방 목록 로드
  const loadRooms = useCallback(async (type: string) => {
    const token = localStorage.getItem("accessToken")
    try {
      const res = await fetch(`${API_BASE}/v1/chats/room?roomType=${type}`, {
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      setRooms(data.result || data.rooms || (Array.isArray(data) ? data : []))
    } catch (e) { console.error(e) }
  }, [])

  // 3. 소켓 연결 및 실시간 구독
  useEffect(() => {
    const token = localStorage.getItem("accessToken")
    if (!token) return router.push("/login")
    
    setUserId(localStorage.getItem("userId") || "")
    setUserName(localStorage.getItem("name") || "사용자")

    const socket = new SockJS(`${API_BASE}/ws/chat`)
    const client = new Client({
      webSocketFactory: () => socket,
      connectHeaders: { Authorization: `Bearer ${token}` },
      onConnect: () => {
        setIsConnected(true)
        loadRooms(activeTab)
      },
      onDisconnect: () => setIsConnected(false),
    })

    client.activate()
    stompClient.current = client

    return () => { client.deactivate() }
  }, [activeTab, loadRooms, router])

  // 4. 방 선택 및 실시간 구독 처리
  const selectRoom = (room: any) => {
    setCurrentRoom(room)
    loadChatHistory(room.chatRoomId)

    // 이미 구독 중이면 중복 구독 방지
    if (!subscriptions.current[room.chatRoomId]) {
      stompClient.current?.subscribe(`/topic/chat.room.${room.chatRoomId}`, (msg) => {
        const received = JSON.parse(msg.body)
        setMessages((prev) => [...prev, { ...received, id: crypto.randomUUID() }])
      })
      subscriptions.current[room.chatRoomId] = true
    }
  }

  // 5. 메시지 전송
  const handleSendMessage = (e: React.FormEvent) => {
    e.preventDefault()
    if (!inputValue.trim() || !currentRoom || !isConnected) return

    stompClient.current?.publish({
      destination: `/app/chat.send/${currentRoom.chatRoomId}`,
      body: JSON.stringify({ 
        content: inputValue, 
        roomType: currentRoom.roomType || activeTab 
      })
    })
    setInputValue("")
  }

  // 자동 스크롤
  useEffect(() => {
    if (scrollRef.current) {
      scrollRef.current.scrollTop = scrollRef.current.scrollHeight
    }
  }, [messages])

  return (
    <div className="flex h-screen bg-white overflow-hidden text-slate-900">
      {/* 사이드바: 다크 스타일 */}
      <aside className="w-80 border-r bg-slate-900 flex flex-col shrink-0">
        <header className="p-6 border-b border-slate-800 flex items-center gap-3">
          <Button variant="ghost" size="icon" onClick={() => router.back()} className="text-slate-400 hover:text-white hover:bg-slate-800">
            <ChevronLeft className="h-5 w-5" />
          </Button>
          <h1 className="font-bold text-lg text-white">채팅 목록</h1>
        </header>

        <div className="p-4">
          <Tabs value={activeTab} onValueChange={(v) => setActiveTab(v)} className="w-full">
            <TabsList className="grid grid-cols-2 w-full bg-slate-800 border-none">
              <TabsTrigger value="GROUP" className="data-[state=active]:bg-primary text-xs font-bold">그룹</TabsTrigger>
              <TabsTrigger value="DIRECT" className="data-[state=active]:bg-primary text-xs font-bold">1:1</TabsTrigger>
            </TabsList>
          </Tabs>
        </div>

        <div className="flex-1 overflow-y-auto space-y-1 p-3">
          {rooms.map((room: any) => (
            <button
              key={room.chatRoomId}
              onClick={() => selectRoom(room)}
              className={`w-full p-4 rounded-2xl flex items-center gap-3 transition-all ${
                currentRoom?.chatRoomId === room.chatRoomId 
                ? 'bg-primary text-white shadow-lg' 
                : 'hover:bg-slate-800 text-slate-400'
              }`}
            >
              <div className={`h-10 w-10 rounded-xl flex items-center justify-center shrink-0 ${currentRoom?.chatRoomId === room.chatRoomId ? 'bg-white/20' : 'bg-slate-800'}`}>
                {activeTab === "GROUP" ? <Users className="h-5 w-5" /> : <User className="h-5 w-5" />}
              </div>
              <div className="text-left overflow-hidden">
                <p className={`font-bold truncate text-sm ${currentRoom?.chatRoomId === room.chatRoomId ? 'text-white' : 'text-slate-200'}`}>{room.title}</p>
                <p className="text-[11px] opacity-60 truncate">새로운 대화가 있나요?</p>
              </div>
            </button>
          ))}
        </div>
      </aside>

      {/* 메인 채팅창: 화이트 스타일 */}
      <main className="flex-1 flex flex-col bg-slate-50">
        {currentRoom ? (
          <>
            <header className="h-16 border-b px-6 flex items-center justify-between bg-white shadow-sm">
              <div className="flex items-center gap-3">
                <div className={`h-2.5 w-2.5 rounded-full ${isConnected ? 'bg-green-500 animate-pulse' : 'bg-slate-300'}`} />
                <h2 className="font-bold text-slate-800">{currentRoom.title}</h2>
              </div>
              <Button variant="ghost" size="sm" className="text-slate-400 hover:text-red-500">
                <LogOut className="h-4 w-4 mr-2" /> 나가기
              </Button>
            </header>

            <div ref={scrollRef} className="flex-1 overflow-y-auto p-6 space-y-6">
              {isLoadingHistory && (
                <div className="flex justify-center p-4">
                  <Loader2 className="h-6 w-6 animate-spin text-primary" />
                </div>
              )}
              {messages.map((msg) => (
                <div key={msg.id} className={`flex gap-3 ${msg.senderId === userId ? "justify-end" : "justify-start"}`}>
                  {msg.senderId !== userId && (
                    <Avatar className="h-9 w-9 border shadow-sm">
                      <AvatarFallback className="bg-slate-200 text-slate-600 text-xs font-bold">
                        {msg.senderName?.charAt(0)}
                      </AvatarFallback>
                    </Avatar>
                  )}
                  <div className={`flex flex-col space-y-1 max-w-[70%] ${msg.senderId === userId ? "items-end" : "items-start"}`}>
                    <span className="text-[10px] text-slate-400 font-bold px-1">{msg.senderName}</span>
                    <Card className={`p-3 text-sm shadow-sm border-none ${
                      msg.senderId === userId 
                      ? "bg-primary text-white rounded-2xl rounded-tr-none" 
                      : "bg-white text-slate-700 rounded-2xl rounded-tl-none"
                    }`}>
                      {msg.content}
                    </Card>
                  </div>
                </div>
              ))}
            </div>

            <form onSubmit={handleSendMessage} className="p-4 bg-white border-t flex gap-2">
              <Input 
                value={inputValue}
                onChange={(e) => setInputValue(e.target.value)}
                placeholder="메시지를 입력하세요..." 
                className="flex-1 h-12 bg-slate-100 border-none focus-visible:ring-primary rounded-xl"
              />
              <Button type="submit" size="icon" className="h-12 w-12 rounded-xl shadow-lg shadow-primary/20">
                <Send className="h-5 w-5" />
              </Button>
            </form>
          </>
        ) : (
          <div className="flex-1 flex flex-col items-center justify-center text-slate-300">
            <div className="w-20 h-20 bg-white rounded-3xl flex items-center justify-center shadow-sm mb-4">
              <MessageSquare className="h-10 w-10 opacity-20" />
            </div>
            <p className="font-bold uppercase tracking-widest text-xs">Select a channel to start</p>
          </div>
        )}
      </main>
    </div>
  )
}